@font-face {
  font-family: 'OpenSans';
  src: url('../fonts/subset-OpenSans.woff2') format('woff2'),
    url('../fonts/subset-OpenSans.woff') format('woff'),
    url('../fonts/subset-OpenSans.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

